.main-Box {
    margin: 20px 2px;
    border-radius: 30px;
    box-shadow: inset 0px 0px 7px 0px rgba(109, 109, 109, 0.397);
    min-height: 512px;
    padding-bottom: 10px;

}

.main-Box-dark {
    box-shadow: 0px 0px 11px 0px rgb(255, 255, 255);
}

.item-Box {
    /* align-items: center; */
    margin: 20px 2px;
    border-radius: 10px;
    padding: 6px;
    box-shadow: 0px 0px 7px 0px #9794946b;
}

.item-Box-light {
    box-shadow: 0px 0px 7px 0px #ffffffdc;
}

.item-Box p {
    margin-bottom: 0px;
}

.check-Box {
    height: 29px;
    width: 28px;
    padding: 2px;
    margin: 2px 10px;
    background-color: #eee;
    border-radius: 50%;
}

.form-check-input:checked {
    background-color: #169411;
    border-color: #2d8305;
}

.form-check-input[type=checkbox] {
    border-radius: 50%;
}

.form-check-inputDark:checked {
    background-color: #263cff;
    border-color: #1c2418;
}

.form-check-inputDark[type=checkbox] {
    border-radius: 2em;
}

@media only screen and (max-width: 419px) {
    .add-Form {
        width: 70%;
    }
}

.tag-Box {
    margin: 10px 1px;
}

.badge-Box {
    margin: 2px 5px;
}

.tag-item-Box {
    box-shadow: 0px 0px 7px 0px #00000078;
    border-radius: 8px;
    max-width: max-content;
    align-items: center;
    justify-content: center;
    margin: 0px 4px;
    cursor: pointer;
}

.tag-item-Box p {
    margin: 0;

}

.tag-selected {
    background-color: #d92e2e;
    color: white;
}
.tag-selected-dark {
    background-color: #d92e2e;
    color: white;
}
.tag-notSelected-dark {
    background-color: #2148f5;
    color: white;
}
/* for disabling the text selected with blue highlight */
.prevent-select {
    -webkit-user-select: none;
    /* Safari */
    -ms-user-select: none;
    /* IE 10 and IE 11 */
    user-select: none;
    /* Standard syntax */
}

.slideWrap{
    overflow: auto;
    display: flex;
    height: 50px;
    /* max-height: 120px; */
}
.slideWrap::-webkit-scrollbar{
    width: 0;
}
